<template lang="pug">
div.map
  baidu-map.bm-view(:center="center" :zoom="zoom" @ready="handler")
    //- bm-geolocation(anchor="BMAP_ANCHOR_BOTTOM_LEFT" :showAddressBar="true" :autoLocation="true" style="button:63px;")
    //- 比例尺
    bm-scale(anchor="BMAP_ANCHOR_TOP_RIGHT")
    //- 导航 上下左右 放大缩小
    bm-navigation(anchor="BMAP_ANCHOR_TOP_RIGHT")
    //- 定位插件
    bm-geolocation(anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true")
    //- 城市列表
    bm-city-list(anchor="BMAP_ANCHOR_TOP_LEFT")
    bm-view.map-view
    //- 搜索组件
    bm-local-search(keyword="医院" :nearby="nearby" :auto-viewport="true" :panel="false")
    //- 圆
    bm-circle(:center="nearby.center" :radius="nearby.radius")
</template>

<script>
// 兼容浏览器与手机端点击事件 解决冲突问题
// const clickEvent = (function () {
//   if ('ontouchstart' in document.documentElement === true) {
//     return 'touchstart';
//   } else {
//     return 'click';
//   }
// })();

export default {
  data () {
    return {
      center: { lng: 104.065, lat: 30.657 },
      zoom: 10,
      nearby: {
        center: {
          lng: 104.065,
          lat: 30.657
        },
        radius: 500
      }
    };
  },
  methods: {
    /**
     * 1.Demo 参考：http://lbsyun.baidu.com/jsdemo.htm#a1_2
     * 2.jsAPI v3.0 类参考: http://lbsyun.baidu.com/cms/jsapi/reference/jsapi_reference_3_0.html#a0b0
     * 3.vue-baidu-map 参考：https://dafrok.github.io/vue-baidu-map/#/zh/index
     */
    // 百度地图加载完成后处理
    handler ({ BMap, map }) {
      map.enableScrollWheelZoom(true); // 允许鼠标滑轮放大缩小 参考:http://lbsyun.baidu.com/cms/jsapi/reference/jsapi_reference_3_0.html#a0b0
      // 百度地图 加载完成
      console.log('map:', map);
      let me = this;
      // 定位
      let geolocation = new BMap.Geolocation();
      let startTime = new Date().getTime();
      console.log('定位前:', startTime);
      geolocation.getCurrentPosition(function(r) {
        let endTime = new Date().getTime();
        console.log('定位后:', endTime, ',耗时:' + ((endTime - startTime) / 1000) + '秒');
        if (this.getStatus() === window.config.BMAP_STATUS_SUCCESS) {
          var mk = new BMap.Marker(r.point);
          me.center = mk.point;
          me.nearby.center = mk.point;
          map.addOverlay(mk);
          map.panTo(r.point);
        } else {
          alert('failed' + this.getStatus());
        }
      }, { enableHighAccuracy: true });
    }
  },
  mounted() {
  }

};
</script>

<style scoped lang="less">
.map {
  height: 100%;
  position: relative;
  .bm-view {
    width: 100%;
    height: 100%;
    .map-view{
      width: 100%;
      height: 100%;
    }
  }
}
</style>
