<template lang="pug">
div.map
  div#map() 高德地图
  div#panel
</template>

<script>
/* global AMap */
/**
 * api 参考：https://lbs.amap.com/api/javascript-api/summary
 * 地图 JS API           -> 示例中心: https://lbs.amap.com/demo-center/js-api  示例代码:https://lbs.amap.com/api/javascript-api/example/map-lifecycle/map-show
 * 地图 JS API UI 组件库 -> 示例中心: https://lbs.amap.com/demo-center/amap-ui  示例代码:https://lbs.amap.com/api/amap-ui/demos/amap-ui-simplemarker/custom-icon
 * 数据可视化 JS API     -> 示例中心: https://lbs.amap.com/demo-center/loca-api 示例代码:https://lbs.amap.com/api/loca-api/demos/loca/loca_create
 */
// 兼容浏览器与手机端点击事件 解决冲突问题
// const clickEvent = (function () {
//   if ('ontouchstart' in document.documentElement === true) {
//     return 'touchstart';
//   } else {
//     return 'click';
//   }
// })();

export default {
  created () {
  },
  mounted() {
    this.initGaodeMap();
  },
  data () {
    return {
      map: null,
      center: { lng: 0, lat: 0 },
      zoom: 3
    };
  },
  methods: {
    // 初始化高德地图
    initGaodeMap () {
      this.map = new AMap.Map('map', {
        resizeEnable: true,
        zoom: 10, // 级别
        center: [104.065, 30.657] // 中心点坐标
        // viewMode: '3D'// 使用3D视图
      });
      // 初始化地图控件
      this.initPlugins();
      // 从高德地图api获取浏览器定位
      this.getLocation();
    },
    // 周边搜索
    placeSearch() {
      // 构造地点查询类
      let placeSearch = new AMap.PlaceSearch({
        type: '医院', // 兴趣点类别
        pageSize: 20, // 单页显示结果条数
        pageIndex: 1, // 页码
        // city: '010', // 兴趣点城市
        citylimit: true, // 是否强制限制在设置的城市内搜索
        map: this.map, // 展现结果的地图实例
        // panel: 'panel', // 结果列表将在此容器中进行展示。
        autoFitView: true // 是否自动调整地图视野使绘制的 Marker点都处于视口的可见范围
      });

      let cpoint = [this.center.lng, this.center.lat]; // 中心点坐标
      console.log('中心点坐标:', cpoint);
      placeSearch.searchNearBy('', cpoint, 1000, function(status, result) {
        console.log('查询结果:', status, result);
      });
    },
    // 从高德地图api获取浏览器定位
    getLocation() {
      let me = this;
      let geolocation = new AMap.Geolocation({
        // 是否使用高精度定位，默认：true
        enableHighAccuracy: true,
        // 设置定位超时时间，默认：无穷大
        timeout: 10000,
        'showButton': true, // 是否显示定位按钮
        'buttonPosition': 'RB', // 定位按钮的位置 /* LT LB RT RB */
        'buttonOffset': new AMap.Pixel(10, 30), // 定位按钮距离对应角落的距离
        'showMarker': true, // 是否显示定位点
        'markerOptions': { // 自定义定位点样式，同Marker的Options
          'offset': new AMap.Pixel(-18, -36),
          'content': '<img src="https://a.amap.com/jsapi_demos/static/resource/img/user.png" style="width:36px;height:36px"/>'
        },
        'showCircle': true, // 是否显示定位精度圈
        'circleOptions': {// 定位精度圈的样式
          'strokeColor': '#0093FF',
          'noSelect': true,
          'strokeOpacity': 0.5,
          'strokeWeight': 1,
          'fillColor': '#02B0FF',
          'fillOpacity': 0.25
        }
      });
      this.map.addControl(geolocation);
      geolocation.getCurrentPosition();
      AMap.event.addListener(geolocation, 'complete', onComplete);
      AMap.event.addListener(geolocation, 'error', onError);

      function onComplete (data) {
        // data是具体的定位信息
        // console.log('data是具体的定位信息:', data);
        // me.center.lng = data.position.lng;
        // me.center.lat = data.position.lat;
        me.center = data.position;
        me.map.setCenter(data.position); // 设置地图中心点
        me.map.setZoom(16); // 设置地图层级
        // 周边搜索
        me.placeSearch();
      }
      function onError (data) {
        console.error('高德地图定位出错!');
        // 定位出错
        // me.getLatLngLocation();
      }
    },
    // getLatLngLocation() {
    //   AMap.plugin('AMap.CitySearch', function () {
    //     let citySearch = new AMap.CitySearch();
    //     citySearch.getLocalCity(function (status, result) {
    //       if (status === 'complete' && result.info === 'OK') {
    //       // 查询成功，result即为当前所在城市信息
    //         AMap.plugin('AMap.Geocoder', function() {
    //           let geocoder = new AMap.Geocoder({
    //           // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
    //             city: result.adcode
    //           });
    //           let lnglat = result.rectangle.split(';')[0].split(',');
    //           geocoder.getAddress(lnglat, function(status, data) {
    //             if (status === 'complete' && data.info === 'OK') {
    //               // data为对应的地理位置详细信息

    //             }
    //           });
    //         });
    //       }
    //     });
    //   });
    // },
    // 初始化地图控件
    initPlugins() {
      // 比例尺
      let scale = new AMap.Scale({
        visible: true,
        offset: new AMap.Pixel(20, 40)
      });
      // 工具条
      let toolBar = new AMap.ToolBar({
        visible: true,
        position: 'RT'
      });
      // 鹰眼
      let overView = new AMap.OverView({
        visible: true
      });
      this.map.addControl(scale);
      this.map.addControl(toolBar);
      this.map.addControl(overView);
      // toolBar.showDirection();
      // toolBar.hideDirection();
      // toolBar.showRuler();
      // toolBar.hideRuler();
      // overView.show(); // overView.hide();
      // overView.open(); //  overView.close();
    }

  }
};
</script>

<style scoped lang="less">
.map{
  height: 100%;
  position: relative;
  #map {
    width: 100%;
    height: 100%;
  }
   #panel {
    position: fixed;
    background-color: white;
    max-height: 50%;
    overflow-y: auto;
    bottom: 30px;
    right: 10px;
    width: 250px;
    border-bottom: solid 1px silver;
  }
}
</style>
